import { sort } from './sort';
export function getComputedAwsReportItems({ report, idKey, labelKey = idKey, sortKey = 'total', sortDirection = 0 /* asc */, }) {
    return sort(getUnsortedComputedAwsReportItems({
        report,
        idKey,
        labelKey,
        sortDirection,
        sortKey,
    }), {
        key: sortKey,
        direction: sortDirection,
    });
}
export function getUnsortedComputedAwsReportItems({ report, idKey, labelKey = idKey, }) {
    if (!report) {
        return [];
    }
    const itemMap = new Map();
    const visitDataPoint = (dataPoint) => {
        if (dataPoint.values) {
            dataPoint.values.forEach(value => {
                const cluster = value.cluster_alias
                    ? value.cluster_alias
                    : value.cluster;
                const total = value.usage ? value.usage.value : value.cost.value;
                const id = value[idKey];
                let label;
                if (value[labelKey] instanceof Object) {
                    label = value[labelKey].value;
                }
                else {
                    label = value[labelKey];
                }
                if (labelKey === 'account' && value.account_alias) {
                    label = value.account_alias;
                }
                if (!itemMap.get(id)) {
                    itemMap.set(id, {
                        cluster,
                        deltaPercent: value.delta_percent,
                        deltaValue: value.delta_value,
                        id,
                        total,
                        label,
                        units: value.usage ? value.usage.units : value.cost.units,
                    });
                    return;
                }
                itemMap.set(id, Object.assign({}, itemMap.get(id), { total: itemMap.get(id).total + total }));
            });
        }
        for (const key in dataPoint) {
            if (dataPoint[key] instanceof Array) {
                return dataPoint[key].forEach(visitDataPoint);
            }
        }
    };
    if (report && report.data) {
        report.data.forEach(visitDataPoint);
    }
    return Array.from(itemMap.values());
}
export function getIdKeyForGroupBy(groupBy = {}) {
    if (groupBy.account) {
        return 'account';
    }
    if (groupBy.instance_type) {
        return 'instance_type';
    }
    if (groupBy.region) {
        return 'region';
    }
    if (groupBy.service) {
        return 'service';
    }
    return 'date';
}
